const sinceRegEx = /\s*([^\s]*)\s*(?:-?\s*([\s\S]*))?\s*$/;
export default function since() {
    return {
        name: "since",
        parse(text) {
            const parsed = sinceRegEx.exec(text);
            const obj = {};
            if (parsed[1]) {
                obj.version = parsed[1];
            }
            if (parsed[2]) {
                obj.description = parsed[2];
            }
            return obj;
        },
    };
}
