import ScssCommentParser from "scss-comment-parser";
import AnnotationsApi from "./annotation.js";
import sorter from "./sorter.js";
import { removeReduntantWhitespace } from "./utils.js";
class Parser {
    annotations;
    scssParser;
    constructor(parserConfig) {
        this.annotations = new AnnotationsApi();
        this.scssParser = new ScssCommentParser(this.annotations.list, parserConfig);
        this.scssParser.commentParser.on("warning", (warning) => {
            console.warn(warning.message);
        });
    }
    async parseString(code, id) {
        let data = this.scssParser.parse(removeReduntantWhitespace(code), id);
        data = sorter(data);
        data = data.map((d) => {
            if (!d.name) {
                // Give everything a default name from context
                d.name = d.context.name;
            }
            return d;
        });
        const promises = [];
        Object.keys(this.annotations.list).forEach((key) => {
            const annotation = this.annotations.list[key];
            if (annotation.resolve) {
                const promise = Promise.resolve(annotation.resolve(data));
                promises.push(promise);
            }
        });
        return Promise.all(promises).then(() => data);
    }
}
/**
 * Try to parse any SassDoc in the SCSS input
 *
 * @example
 *  await parse(`
 *    /// Main color
 *    $stardew: #ffffff;
 *  `);
 */
export async function parse(code, options) {
    const parser = new Parser(options?.parserConfig);
    return await parser.parseString(code, options?.id);
}
